.green-card{background-color:#80b2a3}.blue-card{background-color:#2C6277}.black-font{color:black}*{box-sizing:border-box}html,body{width:100vw;min-height:100vh;margin:0;overflow-x:hidden}#root{width:100%;height:auto;margin:0}#main{display:block;position:relative;width:100%;height:100%;color:white}

