.green-card{background-color:#80b2a3}.blue-card{background-color:#2C6277}.black-font{color:black}h2{font-size:24px}#home{width:100%;min-height:100vh;display:flex;position:relative;flex-direction:column;justify-content:center;align-items:center;background:url(/static/media/table.c717defa.jpeg) no-repeat left top fixed}#home div{display:block;position:relative;padding:10px;text-align:center}#home #nameIntro{font-size:26px;font-family:'Fredoka One', cursive;font-style:italic;text-shadow:4px 4px 6px rgba(0,0,0,0.8)}#home #introContent{font-size:18px;font-family:'Roboto', sans-serif;text-shadow:4px 4px 6px rgba(0,0,0,0.8);-webkit-animation:contentIntro 1.5s ease-in;animation:contentIntro 1.5s ease-in}@-webkit-keyframes contentIntro{0%{opacity:0}100%{opacity:1}}@keyframes contentIntro{0%{opacity:0}100%{opacity:1}}@media screen and (min-width: 450px){h2{font-size:calc(24px + 0.9vw)}h3{font-size:calc(14px + 0.9vw)}#home #nameIntro{font-size:calc(26px + 3vw)}#home #introContent{font-size:calc(18px + 1vw)}}

.green-card{background-color:#80b2a3}.blue-card{background-color:#2C6277}.black-font{color:black}#project{display:flex;position:relative;flex-direction:column;justify-content:center;align-items:center;width:100%;min-height:100vh;padding-top:60px;background-color:#2C6277}#project #project-container{width:100%;height:auto;display:flex;position:relative;flex-direction:row;justify-content:space-around;align-items:center;flex-wrap:wrap;max-width:1380px;padding:20px auto}

.green-card{background-color:#80b2a3}.blue-card{background-color:#2C6277}.black-font{color:black}#contact{width:100%;min-height:100vh;display:flex;position:relative;flex-direction:column;justify-content:space-around;align-items:center;padding-top:60px;background-color:#34383e}#contact form{display:flex;position:relative;flex-direction:column;justify-content:space-around;align-items:center;width:90vw;height:auto;max-width:650px;background-color:rgba(255,255,255,0.8);padding:25px 15px 10px;color:black;border-radius:10px}#contact form div{margin-bottom:20px;width:100%}#contact form label{display:inline-block;position:relative;width:90px}#contact form input,#contact form textarea{width:calc(100% - 90px);min-width:204px;border:1px solid #34383e;border-radius:3px;font-size:16px;line-height:1.7;color:black;padding:0px 10px}#contact form textarea{height:140px}#contact form button{line-height:1.5;font-size:15px;float:right;background-color:#34383e;color:white;padding:5px 15px;border:3px solid #34383e;border-radius:10px;cursor:pointer}#contact form button:active{border:3px inset white}#contact footer{display:flex;position:relative;flex-direction:row;justify-content:center;align-items:center;width:100%;height:auto}#contact footer a{padding:10px;font-size:26px;color:rgba(255,255,255,0.9)}#contact footer a:hover{transform:scale(1.3)}#contact a:hover i{transform:scale(1.3)}#contact a:active i{visibility:hidden}#contact #copyright{font-family:'Pacifico', cursive;font-size:14px}@media screen and (min-width: 650px){#contact form{padding:25px}#contact form div{margin-bottom:15px}#contact form label{width:120px;margin-bottom:10px}#contact form input,#contact form textarea{width:calc(100% - 1px)}#contact form textarea{height:190px}#contact footer a i{font-size:3.2vw}#contact #copyright{font-size:16px}}

.green-card{background-color:#80b2a3}.blue-card{background-color:#2C6277}.black-font{color:black}#navbar{display:flex;position:absolute;flex-direction:row;justify-content:center;align-items:center;width:100%;height:auto;z-index:1000}#navbar nav{width:350px;height:40px;display:flex;position:relative;flex-direction:row;justify-content:center;align-items:center;margin:10px auto;color:white}#navbar nav div{display:inline-block;position:relative;width:auto;height:40px;font-family:'Satisfy', cursive;line-height:2.3;text-align:center;font-size:18px;padding:0px 10px;cursor:pointer;color:white}#navbar nav div:hover{color:rgba(255,0,0,0.7);transform:scale(1.3)}#navbar nav div:active{visibility:hidden}#navbar nav #bio[class*='active']{color:black}.active{border-bottom:2px solid red}#navOne{-webkit-animation:navfirst 2.5s ease-out;animation:navfirst 2.5s ease-out}@-webkit-keyframes navfirst{0%{transform:translate(-75vw, 0)}100%{transform:translate(0, 0)}}@keyframes navfirst{0%{transform:translate(-75vw, 0)}100%{transform:translate(0, 0)}}#navTwo{-webkit-animation:navsecond 2.7s ease-out;animation:navsecond 2.7s ease-out}@-webkit-keyframes navsecond{0%{transform:translate(0, -75vw)}100%{transform:translate(0, 0)}}@keyframes navsecond{0%{transform:translate(0, -75vw)}100%{transform:translate(0, 0)}}#navThree{-webkit-animation:navthree 2.9s ease-out;animation:navthree 2.9s ease-out}@-webkit-keyframes navthree{0%{transform:translate(0, -75vw)}100%{transform:translate(0, 0)}}@keyframes navthree{0%{transform:translate(0, -75vw)}100%{transform:translate(0, 0)}}#navFour{-webkit-animation:navfour 3.1s ease-out;animation:navfour 3.1s ease-out}@-webkit-keyframes navfour{0%{transform:translate(75vw, 0)}100%{transform:translate(0, 0)}}@keyframes navfour{0%{transform:translate(75vw, 0)}100%{transform:translate(0, 0)}}@media screen and (min-width: 450px){#navbar nav{margin:20px auto}#navbar nav div{font-size:calc(18px + .5vw)}}

.green-card{background-color:#80b2a3}.blue-card{background-color:#2C6277}.black-font{color:black}.card-container{display:block;position:relative;width:70vw;height:calc(70vw * 0.77);box-shadow:7px 7px 15px #34383e;border:0;border-radius:10px;min-width:255px;min-height:calc(255px * 0.77);max-width:385px;max-height:calc(385px * 0.77);cursor:pointer;margin:20px auto}.card-container .screenshot,.card-container .screenshot img{display:block;position:relative;width:100%;height:100%;border:0;border-radius:10px;z-index:10}.card-container .card-info{display:none;position:absolute;flex-direction:column;justify-content:space-evenly;align-items:center;width:100%;height:100%;cursor:default;border:0;border-radius:10px;background-color:#fff;color:#2C6277;z-index:20}.card-container .card-info h3{margin:10px 0px 0px}.card-container .card-info div{display:block;position:relative;width:100%;height:100%;text-align:center;padding:5px 0px}.card-container .card-info .project-framework{display:flex;position:relative;width:100%;height:auto;flex-direction:row;justify-content:space-around;align-items:center}.card-container .card-info .project-framework div:hover{transform:scale(1.2)}.card-container .card-info .project-framework div{display:inline-block;position:relative;width:auto;height:auto;cursor:pointer;border-radius:50%;padding:4px;background-color:white}.card-container .card-info .project-framework div span{visibility:hidden;width:auto;height:auto;position:absolute;bottom:125%;left:-50%;padding:5px 10px;z-index:10;opacity:0;transition:opacity 0.3s;color:white;background-color:rgba(0,0,0,0.7);border-radius:6px}.card-container .card-info .project-framework div img{display:block;position:relative;width:calc(24px + 0.8vw);height:calc(24px + 0.8vw)}.card-container .card-info .project-framework div span::after{content:"";position:absolute;top:100%;left:37%;margin-left:-5px;border-width:5px;border-style:solid;border-color:rgba(0,0,0,0.7) transparent transparent transparent}.card-container .card-info .project-framework div:hover span{visibility:visible;opacity:1}.card-container .card-info .project-framework div{border:1px solid #2C6277}.card-container .card-info .card-links{display:flex;position:relative;flex-direction:row;justify-content:space-evenly;align-items:center;width:100%;height:100%}.card-container .card-info .card-links a{color:#2C6277;font-size:30px}.card-container .card-info .card-links a:hover{transform:scale(1.3)}.card-container:hover .screenshot,.card-container:hover .screenshot img{display:none}.card-container:hover .card-info{display:flex}@media screen and (min-width: 751px){.card-container{width:45vw;height:calc(45vw * 0.77)}}@media screen and (min-width: 768px){.card-container{width:35vw;height:calc(35vw * 0.77)}}

.green-card{background-color:#80b2a3}.blue-card{background-color:#2C6277}.black-font{color:black}.about-card{display:flex;position:relative;width:90%;height:210px;flex-direction:column;justify-content:space-evenly;align-items:center;padding:15px;border:0;border-radius:20px;min-height:210px;color:white;max-width:510px;max-height:280px}.about-card h3{margin:0;text-decoration:underline}.about-card .about-card-content{text-align:center}.about-card .about-framework{display:flex;position:relative;width:100%;height:auto;flex-direction:row;justify-content:space-around;align-items:center}.about-card .about-framework div:hover{transform:scale(1.2)}.about-card .about-framework div{display:inline-block;position:relative;width:auto;height:auto;cursor:pointer;border-radius:50%;padding:4px;background-color:white}.about-card .about-framework div span{visibility:hidden;width:auto;height:auto;position:absolute;bottom:125%;left:-50%;padding:5px 10px;z-index:10;opacity:0;transition:opacity 0.3s;color:white;background-color:rgba(0,0,0,0.7);border-radius:6px}.about-card .about-framework div img{display:block;position:relative;width:calc(24px + 0.8vw);height:calc(24px + 0.8vw)}.about-card .about-framework div span::after{content:"";position:absolute;top:100%;left:37%;margin-left:-5px;border-width:5px;border-style:solid;border-color:rgba(0,0,0,0.7) transparent transparent transparent}.about-card .about-framework div:hover span{visibility:visible;opacity:1}.about-card .about-framework div{box-shadow:3px 3px 6px rgba(0,0,0,0.8)}@media screen and (min-width: 940px){.about-card{width:50%;height:35vh;align-self:auto}.about-card .about-framework div{padding:10px}}

